var Vue = require('../lib/vue.js');
var Swiper = require('../lib/swiper.js');

new Vue({
  el: '#m-index',
  data: {
    list: []
  },

  mounted: function () {
    fetch('/api/indexlist').then(response => response.json())
    .then(res => {  
      this.list = res;
      
    })
    .catch(e => console.log("Oops, error", e));
  }
});


//banner
var mySwiper = new Swiper('#banner', {
		autoplay: 5000,//
		initialSlide :0,//
		direction : 'horizontal',//|vertical
		speed:300,//
		autoplayDisableOnInteraction : false,//
		autoplayStopOnLast : false,//trueslideloop
		grabCursor : true,//trueSwiper
		setWrapperSize :true,//Wrapperslidesflexbox
		virtualTranslate : false,//trueSwiper
		roundLengths : true,//trueslide()(border)
		autoHeight: true,//truewrappercontainerslide
	    pagination : '.swiper-pagination',//
	    paginationHide :false,//true
	    paginationType : 'bullets',//:bullets  fraction   progress  custom 
	    paginationClickable :true,//trueSwiper
		lazyLoading : true,//true
		lazyLoadingInPrevNext : true,//slideslide
		lazyLoadingInPrevNextAmount : 2,//11slideslideslide
		loop : true,//
		freeMode : false
	});//mySwiper end





